













































import Vue from 'vue'
export default Vue.extend({
	data(){
		return {
			animationName: 'slide-left'
		}
	},
	watch: {
		'$route'(val){
			if(val.path == '/login') this.animationName = 'slide-left'
			else this.animationName = 'opacity'
		}
	}
})
